/* ======= Star Background ======== */

#starsparent {
	position:absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
	overflow:hidden;
}

#frozenstars {
	background:url(img/stars.jpg) repeat top center;
	position:absolute;
	display:block;
	z-index:0;
	
	/*  -webkit-animation-play-state: paused;
      -moz-animation-play-state: paused;
      -o-animation-play-state: paused;
      animation-play-state: paused; */
}

#tempcenter {
	position:relative;
	width:3px;
	height:3px;
	background-color:red;
	z-index:20;
}
		
#stars {
	background:url(img/stars.jpg) repeat top center;
	position:absolute;
	display:block;
	z-index:-1;
}

/*Show stars at: Slow speed (5%)*/
.starForm{
	position:absolute;
	top:5px;
	right:10px;
	font-size:0.9em;
	background-color:white;
	border-radius: 2px;
	margin:2px;
	padding-left:2px;
}

#starDropdown {
	cursor:pointer;
}


